$red: #AB0000;
$white: #fff;
/* change background color and font color, all fonts to google font */
body {
  background-color: $red;
  font-family: "Play";
  color: $white;
}

/* row spacing */
.burger-row {
  margin-top: 10px;
}

.burger-form-row {
  margin-bottom: 50px;
}

/* input box */
#new-burger-input {
  margin: 0 auto;
  width: 50%;
}

/* button/input box margins */
.btn, #new-burger-input, h1 {
  margin-top: 10px;
  margin-bottom: 10px;
}

/* padding on burger paragraphs */
.burgers-to-eat, .devoured-burgers {
  padding-top: 16px;
}

/* image smaller and centered */
.img-responsive {
  max-width: 200px;
  margin: 0 auto;
}

/* spacing on add burger paragraph */
#add-burger-text {
  margin-top: 5px;
}

